import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import '../../../../styles/AppointmentForm.css';
import { ToastContainer, toast } from 'react-toastify';
import { COMPANY_NAME } from '../../../shared/utility';

function AppointmentForm() {
	useEffect(() => {
		window.scrollTo({ top: 0, behavior: 'smooth' });
	});

	const [patientName, setPatientName] = useState('');
	const [patientNumber, setPatientNumber] = useState('');
	const [patientGender, setPatientGender] = useState('default');
	const [appointmentTime, setAppointmentTime] = useState('');
	const [preferredMode, setPreferredMode] = useState('default');
	const [isSubmitted, setIsSubmitted] = useState(false);
	const [formErrors, setFormErrors] = useState({});

	const handleSubmit = e => {
		e.preventDefault();

		// Validate form inputs
		const errors = {};
		if (!patientName.trim()) {
			errors.patientName = 'Name is required';
		} else if (patientName.trim().length < 8) {
			errors.patientName = 'Name must be at least 8 characters';
		}

		if (!patientNumber.trim()) {
			errors.patientNumber = 'Phone number is required';
		} else if (patientNumber.trim().length < 10) {
			errors.patientNumber = 'Phone number must be of 10 digits';
		}

		if (preferredMode === 'default') {
			errors.preferredMode = 'Please select preferred mode';
		}

		if (Object.keys(errors).length > 0) {
			setFormErrors(errors);
			return;
		}

		// Reset form fields and errors after successful submission
		setPatientName('');
		setPatientNumber('');
		setPatientGender('default');
		setAppointmentTime('');
		setPreferredMode('default');
		setFormErrors({});

		toast.success('Appointment Scheduled !', {
			position: toast.POSITION.TOP_CENTER,
			onOpen: () => setIsSubmitted(true),
			onClose: () => setIsSubmitted(false),
		});
	};

	return (
		<div className="appointment-form-section" style={{ fontFamily: 'var(--app-text-main-font), serif', padding: '10px' }}>
			<Link to="/">{`Home`}</Link>

			<div className="form-container" style={{ fontFamily: 'var(--app-text-main-font), serif', padding: '100px' }}>
				<h2 className="form-title">
					<span>Book Appointment Online</span>
				</h2>

				<form className="form-content" onSubmit={handleSubmit}>
					<label>
						Full Name:
						<input type="text" value={patientName} onChange={e => setPatientName(e.target.value)} required />
						{formErrors.patientName && <p className="error-message">{formErrors.patientName}</p>}
					</label>

					<br />
					<label>
						Phone Number:
						<input type="text" value={patientNumber} onChange={e => setPatientNumber(e.target.value)} required />
						{formErrors.patientNumber && <p className="error-message">{formErrors.patientNumber}</p>}
					</label>

					<br />

					
					<br />
					

					
					<a className="text-appointment-btn" href={`mailto:support@flowhero.app?subject=FlowHeroApps%20Inquiry&body=I would like to learn more from FlowHeroApps. Please contact me at ${patientNumber} and look for ${patientName}`}>Confirm</a>
					<p className="success-message" style={{ display: isSubmitted ? 'block' : 'none' }}>
						Details has been sent.
					</p>
				</form>
			</div>

			<ToastContainer autoClose={5000} limit={1} closeButton={false} />
		</div>
	);
}

export default AppointmentForm;
